/* === GLOBAL STYLES === */
body {
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f4f4f4;
  color: #222;
  transition: background 0.5s, color 0.5s;
}

body.dark {
  background-color: #121212;
  color: #f0f0f0;
}

.app-container {
  max-width: 800px;
  margin: 30px auto;
  padding: 20px;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(12px);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.25);
  transition: background 0.3s ease;
}

body.dark .app-container {
  background: rgba(0, 0, 0, 0.2);
}

/* === HEADINGS === */
h1 {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 20px;
}

/* === LOGIN BUTTON === */
.login-btn {
  display: block;
  margin: 100px auto;
  padding: 14px 24px;
  font-size: 18px;
  border: none;
  border-radius: 12px;
  background: linear-gradient(135deg, #007bff, #0056b3);
  color: white;
  cursor: pointer;
  box-shadow: 0 4px 10px rgba(0, 123, 255, 0.5);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.login-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 14px rgba(0, 123, 255, 0.6);
}

/* === USER INFO HEADER === */
.user-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.user-info img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.user-info span {
  font-weight: 500;
  margin-left: 10px;
}

.user-info button {
  background: #ff4d4d;
  color: white;
  border: none;
  padding: 8px 14px;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.user-info button:hover {
  background: #cc0000;
}

/* === CHAT BOX === */
.chat-box {
  height: 400px;
  overflow-y: auto;
  border-radius: 16px;
  padding: 15px;
  background: rgba(255, 255, 255, 0.3);
  transition: background 0.3s;
  scrollbar-width: thin;
}

body.dark .chat-box {
  background: rgba(0, 0, 0, 0.4);
}

.chat-box::-webkit-scrollbar {
  width: 8px;
}
.chat-box::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

/* === MESSAGE STYLES === */
.message {
  display: flex;
  margin: 12px 0;
  align-items: flex-end;
  transition: all 0.3s ease-in-out;
}

.message.own-message {
  flex-direction: row-reverse;
}

.message img {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  margin: 0 10px;
}

.message-content {
  max-width: 70%;
  padding: 10px 14px;
  border-radius: 18px;
  background: #e0e0e0;
  position: relative;
  animation: fadeInUp 0.4s ease;
}

.message.own-message .message-content {
  background: #007bff;
  color: #fff;
}

body.dark .message-content {
  background: #2a2a2a;
}

body.dark .own-message .message-content {
  background: #0056b3;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.timestamp {
  font-size: 11px;
  margin-top: 6px;
  color: #999;
  opacity: 0.7;
}

/* === TYPING INDICATOR === */
.typing-indicator {
  font-style: italic;
  color: #999;
  margin: 10px 0;
  animation: typingBlink 1.2s infinite;
}

@keyframes typingBlink {
  0% { opacity: 0.3; }
  50% { opacity: 1; }
  100% { opacity: 0.3; }
}

/* === INPUT AREA === */
.input-area {
  display: flex;
  gap: 10px;
  margin-top: 15px;
  align-items: center;
}

.input-area input {
  flex-grow: 1;
  padding: 12px 16px;
  font-size: 16px;
  border-radius: 10px;
  border: 1px solid #ccc;
  outline: none;
  transition: border 0.2s ease;
}

.input-area input:focus {
  border-color: #007bff;
}

.input-area button {
  padding: 10px 16px;
  border: none;
  background: #007bff;
  color: white;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  transition: background 0.3s ease;
}

.input-area button:hover {
  background: #0056b3;
}

/* === EMOJI PICKER === */
.emoji-picker {
  position: absolute;
  bottom: 80px;
  right: 20px;
  z-index: 10;
  border-radius: 12px;
  box-shadow: 0 8px 20px rgba(0,0,0,0.2);
  animation: slideUp 0.3s ease;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* === EDIT/DELETE POPUP === */
.edit-delete-popup {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 6px;
}

.edit-delete-popup button {
  background: none;
  border: none;
  color: #888;
  font-size: 16px;
  cursor: pointer;
}

.edit-delete-popup button:hover {
  color: #ff4d4d;
}
